import Vue from "vue";
import Vuex from "vuex";
import _ from "lodash";
import { db } from '@/api/firebase';
// import { vuexfireMutations, firestoreAction } from 'vuexfire'

const AS_SNAPSHOT = 'AS_SNAPSHOT'

const defn = (array:any[]) => {
  return array.reduce((obj:any, item:any) => {
    obj[item.id] = item.data()
    return obj;
  }, {});
}

Vue.use(Vuex);

const store = new Vuex.Store({
  modules: {},
  state: {
    $db: <firebase.firestore.Firestore>db,
    // _flownBack: false,
    prodc: <any>{},
    prodc_raw: <any>{},
    lastFetch: <any>null,
    // ProdcDB: db.collection('prodc').
  },
  mutations: {
    // ...vuexfireMutations,
    assignProdc: (state, eProdc) => {
      state.prodc = _.assign(state.prodc, eProdc);
    },
    reAssignPr: (state, brandNewPr:any) => {
      state.prodc = brandNewPr
    },
    stampFetch: (state, date?) => {
      state.lastFetch = !date ? new Date(): new Date(date);
    },
    // stmpDelete: (state, key:string) => {
    //   state.prodc[key].status = 'PENDING_DELETION'
    // },
  },
  getters: {
    prodc$db: async ({$db})=> {
      const { docs } = await $db.collection('prodc').get()
      return defn(docs)
    },
    prodc_: ({prodc}) => {
      return prodc
    },
  },
  actions: {
    fetchNewData: ({ commit }, newSnapshot: any) => {
      newSnapshot.forEach((doc: any) => {
        const eProdc = _.zipObject([doc.id], [doc.data()]);
        commit("assignProdc", eProdc);
      });
      commit("stampFetch");
    },
    [AS_SNAPSHOT]: ({ commit }, QuerySnapshot: firebase.firestore.QuerySnapshot) => {
      commit('reAssignPr', defn(QuerySnapshot.docs))
      console.log(QuerySnapshot.docChanges());
    },
    StampDelete: ({ commit, state }, key:string)=> {
      state.$db.collection('prodc').doc(key).update({status: 'PENDING_DELETION'})
      // commit('stmpDelete', key)
    },
    // bindRef: firestoreAction(({ bindFirestoreRef })=> {
    //   return bindFirestoreRef('prodc', db.collection('prodocy'));
    // }),
    // unbindRef: firestoreAction(({ unbindFirestoreRef }) => {
    //   return unbindFirestoreRef('prodc')
    // })
  }
});

export default store;
